.owl-gallery-look {
    //height: 230px;
    @include sm {
        display: none!important; } }


.item-look {
    float: left;
    background: transparent;
    padding: 15px;
    //width: 33%;
    height: 230px;

    z-index: 1;
    position: relative;
    @include md {
        width: 48%; }
    @include sm {
        width: 100%; }

    &__row {
        min-height: 55px;
        display: block;
        width: 100%;
        margin-bottom: 15px;
        position: relative; }
    .img-block {
        @include pseudo;
        text-align: center;
        position: relative;
        width: 100%;
        display: table;
        img {
            width: 100%;
            height: auto; } }


    &__title {
        font-size: 12px;
        font-weight: normal;
        color: #949494;
        line-height: 14px;
        height: 16px;
        overflow: hidden;
        text-transform: uppercase;
        @include md {
            height: 30px; }
        a {
            color: #949494; } } }


.look {
    &__element {
        @include clr;
        position: relative;
        //max-height: 400px;
        width: 100%;
        margin-left: .5em;
 }        //margin-top: 1%;
    &__full {
        float: left;
        display: block;
        margin-right: 2em;
        @include sm {
            float: none!important;
            width: 100%; }
        .img-block img {
            height: 550px;
            width: auto;
            display: block;
            @include md {
                height: 350px; }
            @include sm {
                height: 250px; } } }
    &__parts {
        float: left;
        @include sm {
            float: none!important;
            width: 100%; }
        &-element {
            display: table;
            margin-bottom: 1em;
            * {
                display: table-cell;
                vertical-align: middle; } }
        &-element-pic {
            padding: 10px;
            background: #fff; }
        &-element-title {
            padding-left: 15px;
            .price {
                color: #949494;
                font-weight: 500;
                .sale {
                    color: #F24B4B;
                    padding-left: 5px; } } } } }

.page__catalog {}
.owl-gallery {
    .item {
        width: 100%;
 } }        //height: 250px;
.owl-carousel .owl-nav {
    bottom: initial;
    top: -100px;
    right: 10%;
    left: inherit; }

