.owl-carousel {
    //height: 100vh;
    width: 100%; }




.hero {
    &__item {
        width: 100%;
        height: 500px;
        position: relative;
        display: table;
        @include sm {
            height: 300px;
            margin-top: 2em; } }
    &__img {
        width: 100%;
        height: 100%;
        background-size: contain;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 80% 100%;
        position: absolute;
        z-index: 10; }
    &__item {
        position: relative;
        @include pseudo;
        &:before {
            display: none; }
        &:after {
            z-index: 0;
            background: #000;
            width: 100%;
            height: 1px;
            top: 50%; } }
    &__info {
        // display: table-cell;
        // vertical-align: middle;
        z-index: 20;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0,-50%); }
    &__title {
        font-size: 18px;
        padding-bottom: .5em;
        //border-bottom: 1px solid #333;
        padding: 0;
        @include md {
            background: rgba(255, 255, 255, 0.75); }
        &.special {
            text-transform: uppercase;
            font-size: 36px;
            font-weight: 100;
            padding: 0;
            margin-top: -20px; } }
    &__link {
        font-size: 14px;
        padding: 0; } }

.owl-oneslide {}


.owl-gallery {
    margin-right: -20px; }

.owl-carousel .owl-dots {
    position: absolute;
    bottom: 5%;
    left: 5%;
    .ow-dot span {
        background: #97989A; }
    .ow-dot.active span {
        background: #000000; } }

.owl-carousel.owl-gallery .owl-dots {
 }    //display: none;
.owl-carousel.owl-oneslide .owl-nav {
 }    //display: none;

.owl-carousel.owl-gallery .owl-nav {
    position: absolute;
    top: -50px;
    right: 5%;
    button {
        margin: 0; }
    .owl-prev span {
        border-right: 0; }
    button span {
        height: 30px;
        border: 1px solid #ccc;
        padding: 5px 20px;
        color: #000; } }

.owl-carousel.owl-gallery.owl-theme .owl-nav [class*=owl-]:hover {
    background: none; }

.owl-gallery {
    @include clr;
    display: block;
    width: 100%;
    z-index: 1; }
