$gray: #ccc;
//$light: #eee;

.search {
    width: 320px;
    position: relative;
    @include r(1800) {
        width: 200px; }
    @include r(1480) {
        width: auto;
        // +rmin(668)
        @include rmin(1200) {
            width: auto;
            position: relative;
            text-align: right; } }
    &__input {
        border-radius: 5px;
        padding: 0 30px 0 11px;
        height: 30px;
        border: 1px solid $gray;
        width: 100%;
        font-size: 14px;
        //+reg
        ::-ms-clear {
            display: none; }
        @include placeholder {
            text-transform: uppercase;
            color: #000;
            opacity: .5;
            letter-spacing: 1px;
 }            //+light
        @include r(1480) {
            // +rmin(668)
            @include rmin(1200) {
                width: 300px;
                position: absolute;
                right: 0;
                visibility: hidden;
                opacity: 0;
                transition: visibility .3s, opacity .3s;
                &.is-open {
                    visibility: visible;
                    opacity: 1; } } }
        @include xs {
            height: 40px;
            line-height: 40px;
            font-size: 18px; } }
    &__btn {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        margin: auto;
        background: transparent;
        width: 30px;
        @include r(1480) {
            // +rmin(668)
            @include rmin(1200) {
                height: 30px;
                position: relative;
                z-index: 2; } }
        @include xs {
            //background: $accent
            width: 50px;
            border-radius: 0 4px 4px 0; }
        .icon {
            //fill: $accent_blue
            font-size: 16px;
            margin-top: 2px;
            @include xs {
                fill: #fff; } } } }

.input {
    @include clr;
    input {
        height: 44px;
        line-height: normal;
        //+light
        width: 100%;
        border: 1px solid $gray;
        border-radius: 4px;
        padding: 0 20px;
        font-size: 16px; }
    .input__short {
        width: 30%;
        float: left; }
    .input__long {
        width: 66%;
        float: right; } }
.input_big {
    input {
        height: 48px; } }


